import React, { useState, useEffect } from "react";
import "../App.css";
import Slider from "@material-ui/core/Slider";
const constant = 2;
function MaskDetector() {
  const [coords, setcoords] = useState({ lat: "", lng: "" });
  const [value, setValue] = React.useState(30);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const [result, setrestult] = useState(
    localStorage.getItem("vOneLocalStorage") === "true"
  );
  function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
    });
    useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
        // Set window width/height to state
        setWindowSize({
          width: window.innerWidth,
          height: window.innerHeight,
        });
      }
      // Add event listener
      window.addEventListener("resize", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize();
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount
    return windowSize;
  }
  function showNotification() {
    const notification = new Notification("Location Nearby", {
      body: "Cornajlak dasdljbk kvkhvkjhvk Mate how are ya?",
    });
  }
  const size = useWindowSize();
  useEffect(() => {
    if (Notification.permission === "granted") {
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission().then((permission) => {
        if (Notification.permission === "granted") {
          showNotification();
        }
      });
    }
  }, [constant]);
  async function getLocation() {
    if (await navigator.geolocation) {
      await navigator.geolocation.getCurrentPosition(
        showPosition,
        error,
        options
      );
    } else {
      console.log("Error");
    }
  }
  function error(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);
  }
  var options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0,
  };
  async function showPosition(position) {
    let latitude = await position.coords.latitude;
    let longitude = await position.coords.longitude;
    setcoords({
      lat: latitude,
      lng: longitude,
    });
  }
  let heighty = size.height / 2;
  let heightyyy = (size.height / 3.5).toFixed(0);
  let heightyy = heighty.toFixed(0);
  useEffect(() => {
    var vOneLS = localStorage.getItem("vOneLocalStorage");
    window.addEventListener("storage", () => {
      var vOneLS = localStorage.getItem("vOneLocalStorage");
      setrestult(vOneLS);
    });
  }, []);
  return (
    <>
      <div
        className="App"
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          ></div>
          <div
            style={{
              texAlign: "center",
              paddingTop: "10vh",
              paddingBottom: "0",
              marginBottom: "0",
            }}
          >
            <h2 style={{ color: "#69B0E8" }}>Verification</h2>
          </div>

          <p
            style={{
              color: "#69B0E8",
              marginTop: -10,
            }}
          >
            Please verify your mask
          </p>
          <embed
            class="major"
            src="https://ayush4921.github.io/reactappstatic/mywebcam.html"
            title="ads"
            style={{
              width: `${heightyy}px`,
              height: `${heightyy}px`,
              overflow: "hidden",
              marginTop: "0px",
              marginRight: "0px",
              marginLeft: "0px",
              padding: 0,
              marginBottom: "0px",
              paddingBottom: "0px",
            }}
          ></embed>
        </div>
        <br></br>
        <div>
          {result === "Correct: Mask On" && (
            <img height="150px" src="check.gif"></img>
          )}
        </div>
        {result === "Correct: Mask On" && (
          <h3 style={{ color: "green" }}>Correct: Mask On</h3>
        )}

        {result === "Wrong: No Mask" && (
          <h3 style={{ color: "red" }}>Please wear your mask</h3>
        )}
        {result === "Wrong: Nose Visible" && (
          <h3 style={{ color: "red" }}>Please wear your mask above the nose</h3>
        )}
      </div>
      <br></br>
    </>
  );
}
export default MaskDetector;
