import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import Icon from "@material-ui/core/Icon";
import firebase from "firebase/app";
import Avatar from "@material-ui/core/Avatar";
import { Link } from "react-router-dom";
import TemporaryDrawer from "./sidebar";
import { auth, db } from "./firebase";
var provider = new firebase.auth.GoogleAuthProvider();
auth.setPersistence(firebase.auth.Auth.Persistence.LOCAL);
//import SvgIcon from '@material-ui/core/SvgIcon';

//import MenuIcon from '@material-ui/icons/Menu';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

function logout() {
  auth
    .signOut()
    .then(function () {
      alert("Logout successful");
    })
    .catch(function (error) {
      alert("An error occured");
    });
}

function signinwithgoogle() {
  auth
    .signInWithPopup(provider)
    .then(function (result) {
      // This gives you a Google Access Token. You can use it to access the Google API.
    })
    .catch(function (error) {
      // Handle Errors here.
      //var errorCode = error.code;
      var errorMessage = error.message;
      // The email of the user's account used.
      //var email = error.email;
      // The firebase.auth.AuthCredential type that was used.
      //var credential = error.credential;
      alert(errorMessage);
      // ...
    });
}

function ButtonAppBar(props) {
  const [loggedin, setloggedin] = useState(false);
  const [userdata, setUserdata] = useState({});
  auth.onAuthStateChanged(function (user) {
    if (user) {
      var myuser = auth.currentUser;
      /*var Username, email, photoUrl, uid, emailVerified;
        Username = user.displayName;
        email = user.email;
        photoUrl = user.photoURL;
        emailVerified = user.emailVerified;
        uid = user.uid;*/

      setUserdata(myuser);
      setloggedin(true);
    } else {
      setloggedin(false);
    }
  });

  const classes = useStyles();

  return (
    <div className={classes.root} id="Menu">
      <AppBar color="transparent" elevation={0}>
        <Toolbar>
          <TemporaryDrawer />

          <Typography variant="h6" className={classes.title}>
            <Link
              to="/"
              style={{ textDecoration: "none", color: "white" }}
            ></Link>
          </Typography>
          <Button> </Button>
          {!loggedin ? (
            <Button onClick={() => signinwithgoogle()} color="inherit">
              <Icon
                className="fab fa-google"
                style={{ paddingLeft: 20, fontSize: 30 }}
              />
            </Button>
          ) : (
            <>
              <Button color="inherit" onClick={() => logout()}>
                LogOut
              </Button>
              <Avatar alt={userdata.displayName} src={userdata.photoURL} />
            </>
          )}
        </Toolbar>
      </AppBar>
    </div>
  );
}

export default ButtonAppBar;
