import React, { useState, useEffect } from "react";
import "../App.css";
import myLogo from "../logo.svg";

import Slider from "@material-ui/core/Slider";
import { auth, db } from "../firebase";
import { withStyles, createStyles } from "@material-ui/core";
const constant = 2;
function Home() {
  const [loggedin, setloggedin] = useState(false);
  const [userdata, setUserdata] = useState({});
  auth.onAuthStateChanged(function (user) {
    if (user) {
      var myuser = auth.currentUser;
      /*var Username, email, photoUrl, uid, emailVerified;
        Username = user.displayName;
        email = user.email;
        photoUrl = user.photoURL;
        emailVerified = user.emailVerified;
        uid = user.uid;*/
      setUserdata(myuser.displayName);
      setloggedin(true);
    } else {
      setloggedin(false);
    }
  });
  const CustomSlider = withStyles({
    root: {
      color: "#69B0E8",
      height: 3,
      padding: "13px 0",
    },
    track: {
      height: 4,
      borderRadius: 2,
    },
    thumb: {
      height: 20,
      width: 20,
      backgroundColor: "#fff",
      border: "1px solid currentColor",
      marginTop: -9,
      marginLeft: -11,
      boxShadow: "#ebebeb 0 2px 2px",
      "&:focus, &:hover, &$active": {
        boxShadow: "#ccc 0 2px 3px 1px",
      },
      color: "#fff",
    },
  })(Slider);
  const [coords, setcoords] = useState({ lat: "", lng: "" });
  const [value, setValue] = React.useState(30);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const [result, setrestult] = useState(
    localStorage.getItem("vOneLocalStorage") === "true"
  );
  function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
    });
    useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
        // Set window width/height to state
        setWindowSize({
          width: window.innerWidth,
          height: window.innerHeight,
        });
      }
      // Add event listener
      window.addEventListener("resize", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize();
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount
    return windowSize;
  }
  function showNotification() {
    const notification = new Notification("Location Nearby", {
      body: "Cornajlak dasdljbk kvkhvkjhvk Mate how are ya?",
    });
  }
  const size = useWindowSize();
  useEffect(() => {
    if (Notification.permission === "granted") {
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission().then((permission) => {
        if (Notification.permission === "granted") {
          showNotification();
        }
      });
    }
  }, [constant]);
  async function getLocation() {
    if (await navigator.geolocation) {
      await navigator.geolocation.getCurrentPosition(
        showPosition,
        error,
        options
      );
    } else {
      console.log("Error");
    }
  }
  function error(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);
  }
  var options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0,
  };
  async function showPosition(position) {
    let latitude = await position.coords.latitude;
    let longitude = await position.coords.longitude;
    setcoords({
      lat: latitude,
      lng: longitude,
    });
  }
  let heighty = size.height / 2;
  let heightyyy = (size.height / 3.5).toFixed(0);
  let heightyy = heighty.toFixed(0);
  useEffect(() => {
    var vOneLS = localStorage.getItem("vOneLocalStorage");
    window.addEventListener("storage", () => {
      var vOneLS = localStorage.getItem("vOneLocalStorage");
      setrestult(vOneLS);
    });
  }, []);
  useEffect(() => {
    getLocation();
  }, []);
  return (
    <>
      <div
        className="App"
        style={{
          display: "flex",
          width: "100%",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <div
          style={{
            display: "flex",
            width: "100vh",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <div
            style={{
              display: "flex",
              width: "100vh",
              flexDirection: "column",
              alignItems: "center",
            }}
          ></div>
        </div>
        <img
          style={{
            padding: 0,
            marginBottom: 0,
            height: heightyy,
          }}
          src={`https://maps.google.com/maps/api/staticmap?center=${coords.lat},${coords.lng}&markers=color:blue%7Clabel:S%7C${coords.lat},${coords.lng}&zoom=15&size=${size.width}x${heightyy}&sensor=false&key=AIzaSyAeOWo7Lh4euA9BQ28B_kIc7iehc_KKEhc`}
        ></img>
        <div
          style={{
            backgroundColor: "#414141",
            height: heighty + 15,
            width: size.width,
            borderRadius: "20px 20px 0px 0px",
            marginTop: -15,
          }}
        >
          <br></br>
          <h2 style={{ color: "white" }}>Welcome {loggedin && userdata}</h2>
          <p>Select the radius of your location</p>
          <CustomSlider
            aria-labelledby="discrete-slider-small-steps"
            step={10}
            marks
            min={20}
            max={100}
            value={value}
            onChange={handleChange}
            style={{ width: "75%" }}
          />
          <br></br>

          <button
            id="locationbutton"
            onClick={() => {
              showNotification();
            }}
            style={{
              fontSize: "15px",
              marginTop: "30px",
              backgroundColor: "#69B0E8",
              borderRadius: "10px",
            }}
          >
            Save Location
          </button>
        </div>
      </div>
    </>
  );
}
export default Home;
